<template>
    <div class="headlines">
        <div class="title-content clearfix">
            <div class="title-box fl">
                <div class="t-text">头条>>></div>
            </div>
           <div class="line-box fl"></div>
            <div class="line-box fl"></div>
            <div class="ad-img-box fr">
                <a target="_blank" href="https://g.163.com/c?AID=259321&amp;FlightID=2057&amp;Values=339870558&amp;Redirect=http://data.163.com/special/datablog/">
                    <img class="ad-img auto-img" src="https://yt-adp.ws.126.net/channel4/1200125_roku_20190313.jpg" border="0"  alt="">
                </a>
            </div>
        </div>

        <div class="newList-content">
        <div class="clearfix">
            <div class="new-list-left-box fl">
                <div class="list-item clearfix" v-for="(item,index) in HeadlinesListData" :key="index" >
                    <div class="left-img fl">
                        <el-image class=" err-imgs">
                            <div slot="error" class="image-slot">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                        <img class="a-img auto-img" :src="item.images" alt="">
                    </div>
                    <div class="right-cont fl">
                        <div class="title">{{item.title}}</div>
                        <div class="text-cont">
                            <span class="post-name">{{item.posterNickName}}</span>
                            <span class="pub-time">日期：{{item.publishTime}}</span>
                            <span class="comment-count">评论：{{item.commentCount}}</span>
                        </div>
                    </div>
                </div>
                <div class="pagination-page">
                    <el-pagination 
                    class="pag-box" 
                    background 
                    layout="prev, pager, next" 
                    :size-change="10"
                    :total="1000"
                    :current-change = "currentPage"
                    @current-change="handleCurrentChange"
                    >
                    </el-pagination>
                </div>
            </div>

            <div class="new-other-right-box fl">
                <div class="other-list">
                    <div class="public-benefit-advertise">
                        <div class="pub-ad-title">公益·广告</div>
                    </div>
                       <div class="advertise-three">
                       <img class="imgs" src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3963176046,4154651129&fm=26&gp=0.jpg" alt="">
                        <div class="title">[公益之家]——拒绝酒驾</div>
                        <div class="texts">开车不喝酒，喝酒不开车！拒绝酒驾，珍惜生命。</div>
                    </div>
                     <div class="advertise-two">
                        <el-carousel height="200px">
                            <el-carousel-item v-for="item in adbannerData" :key="item">
                                <img class="imgs auto-img" :src="item" alt="">
                            </el-carousel-item>
                        </el-carousel>
                    </div>
                    <div class="advertise-one">
                        <div class="ad-box">   
                            <img class="ad-imgs auto-img" src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2559058049,1882169172&fm=26&gp=0.jpg" alt="">
                            <div class="ad-texts">广告</div>
                        </div>
                        <div class="ad-title">自然堂·亮-润-白</div> 
                    </div>



                </div>
            </div>
        </div>
    </div>

    </div>
</template>

<script>
    import "../../assets/css/headlines.less";
    export default {
        name:'headlines',
        data(){
            return {
                HeadlinesListData:[],
                pageNum:'',
                currentPage:1,
                 adbannerData:[
                     'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=813059290,2999325279&fm=26&gp=0.jpg',
                     'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3036988705,1298077967&fm=26&gp=0.jpg',
                     'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=371673075,3441510009&fm=26&gp=0.jpg',
                 ],
            }
        },
        created(){
            this. getHeadlinesListData()
        },
        methods:{

            getHeadlinesListData(pageNum){

                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey:this.appkey,
                        category:'T1348647909107',
                        page:pageNum,
                    }
                }).then((result) => {
                    // console.log('result',result)
                    this.HeadlinesListData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getHeadlinesListData(pageNum)
            
            }
        }
    }
</script>

<style lang="less" scoped>

</style>